<template>
  <div class="locationSelect shadow-10">
    <div class="row cross-center clickable h-100" @click="showLocationList = !showLocationList">
      <div class="flex">{{ selectedLocation.name }}</div>
      <div class="w-10">▾</div>
    </div>
    <div class="locationList" v-if="showLocationList">
      <div v-for="location in locationList" :key="location['city_id']" class="clickable" @click="selectLocation(location)">{{ location.name }}</div>
    </div>
  </div>
</template>
<script>
import cities from '../assets/js/cities';

export default {
  name: 'CalLocationSelect',
	computed: {
		selectedLocation() {
			return this.$store.state.selectedLocation;
		}
	},
  data() {
    return {
      showLocationList: false,
			locationList: cities
    }
  },
	created() {
		const vm = this;

		const location = localStorage.location;

		if (!location || (location != '43' && location != '52')) {
			location = '43';
		}

		_.each(cities, city => {
			if(city['city_id'] == location) {
				vm.$store.dispatch('selectLocation', { location: city, callback: vm.loadLocation });
				return false;
			}
		});
	},
	methods: {
		loadLocation(locationObj) {
			const vm = this;
			vm.$store.dispatch('loadLocation', locationObj)
				.then(dateToselect => {
					vm.$store.dispatch('selectDate', dateToselect);
				});
		},
		selectLocation(location) {
			let vm = this;

			if (location['city_id'] != 43 && location['city_id'] != 52) {
				location = _.find(cities, {'city_id': 43});
			}

			vm.$store.dispatch('selectLocation', { location, callback: vm.loadLocation });
			vm.showLocationList = false;
		}
	}
}
</script>
