	& div:nth-of-type(1) {
		padding-left: 10px;
	}

	& div:nth-child(1) div:nth-child(2) {
		font-size: 25px;
	}
}

.locationList {
	position: absolute;
	background-color: white;
	box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.3);
	min-width: 200px;
	overflow-y: auto;
	z-index: 2;
	max-height: 500px;

	&>div {
		padding: 10px;
	}
}

@media screen and (min-width: 768px) {
	.locationList,
	.locationSelect {
		width: 35%;
	}

	.locationSelect {
